@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');
/* base styles */
* { 
  /* Applies to all HTML */
  margin: 0;
  font-family: 'Times New Roman', Times, serif;
  color: #333;
}

body {
	margin-bottom: 25px;
}

.content {
  max-width: 600px;
  margin: 0px auto;
  padding: 20px;
}

/* HOME CSS */

div.home, div.portfolio, div.cv_resume, div.running_page {
  text-align: center;
}

p.name, p.cv_resume_heading {
  /* font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
  font-family: 'Baskerville';
  font-size: 40px;
}

p.name:hover{
  color: #B00000;
}

.headshot {
  margin: 8px 20px 20px 0px;
  /* border-radius: 50%; */
  max-height: 200px;
  float: left;
}

.bio {
  font-family: 'Baskerville';
  text-align: justify;
}

.running {
	font-family: 'Baskerville';
	text-align: justify;
  }

.running_goal {
	font-family: 'Baskerville';
	color: teal;

}

p.bio a:hover, div.running_page a:hover {
	color: #B00000;
}

div.home a, div.running_page a {
	font-family: 'Baskerville';
	text-align: justify;	
	text-decoration: underline dotted 1px #B00000;
	text-underline-offset: 2px;
	/* text-decoration-line: 1px bottom dotted; */
	/* color: #B00000 */
	color: #333;
}

/* PORTFOLIO CSS */

p.portfolio_text {
	font-family: 'Baskerville';
}

.portfolio_heading {
	font-family: 'Baskerville';
	font-size: 35px;

}

/* CONTACT FORM CSS */
div.contact {
	max-width: 400px;
	margin: 0 auto;
	text-align: center;
}

h2.contact_heading {
	font-family: 'Baskerville';
	font-size: 18px;
	text-align: justify;
	padding-top: 10px;
	padding-bottom: 20px;
}

.contact label {
	text-align: left;
	display: block;
	font-family: 'Baskerville';
}

.contact input, .contact textarea {
	width: 100%;
	padding: 6px 10px;
	margin: 10px 0;
	border: 1px solid #ddd;
	box-sizing: border-box;
	display: block;
}

.contact .button {
	font-family: 'Baskerville';
	font-size: 18px;
	color: #B00000;
	background-color: #fff;
	border: 1px solid #333;
	padding: 8px;
	border-radius: 8px;
	cursor: pointer;
}

/* NAVBAR CSS */

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  max-width: 100%;
}

.navbar .links {
  margin-left: auto;
  margin-right: auto;
}


.navbar a {
  margin-left: 16px;
  padding: 6px;
  font-family: 'Quicksand';
  color: #333;
  text-decoration: none;
}

.navbar a:hover{
  color: #888 !important;
}

.navbar .nav-is-active {
	color: #B00000 !important;	
	font-weight: 500;
}




/* FOOTER CSS */

.footer{
  padding: 10px 15px 0px 15px;
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: white;
}

.footer a {
  margin-left: 20px;
  fill: #d3d3d3;

}

.footer p.copyright {
	/* font-family: "Baskerville"; */
	font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
	font-size: 12px;
	margin-top: 5px;
	margin-bottom: 0px;
	color: grey;
}

svg {
  width: "20px";
  height: "20px";
  fill: grey;
  pointer-events: all;
}

svg:hover{
  fill: #187bcd;
  pointer-events: all;
}